.decorationWrapper{display:flex;align-items:center;justify-content:center;position:absolute;z-index:10;overflow:visible;background-color:#fffce3;border-radius:50%;width:80px;height:80px}.decorationWrapper.oval{width:100px;height:80px;border-radius:40px}.decorationWrapper.outline{border:3px solid #161616;background-color:rgba(0,0,0,0)}.decorationContent{width:100%;height:100%;display:flex;justify-content:center;align-items:center;position:relative;z-index:2}.topLeftXAxis{left:-15px;transform:rotate(270deg);top:50%}.topLeftYAxis{top:-15px;left:50%}.topCenterXAxis{top:-15px;left:50%}.topCenterYAxis{left:0;top:50%;transform:rotate(90deg)}.topRightXAxis{right:-15px;transform:rotate(90deg);top:50%}.topRightYAxis{top:-15px;right:50%}.middleLeftXAxis{left:-15px;top:50%;transform:rotate(270deg)}.middleLeftYAxis{left:15px;top:0}.middleCenterXAxis{left:50%;top:-15px}.middleCenterYAxis{top:50%;left:-15px;transform:rotate(270deg)}.middleRightXAxis{right:-15px;top:50%;transform:rotate(90deg)}.middleRightYAxis{right:15px;top:0}.bottomLeftXAxis{left:-15px;bottom:50%;transform:rotate(270deg)}.bottomLeftYAxis{bottom:-15px;left:50%}.bottomCenterXAxis{bottom:-15px;left:50%}.bottomCenterYAxis{bottom:50%;left:-15px;transform:rotate(270deg)}.bottomRightXAxis{right:-15px;bottom:50%;transform:rotate(90deg)}.bottomRightYAxis{bottom:-15px;right:50%}.starXAxis{left:-15px;bottom:20px;transform:rotate(270deg)}.starYAxis{bottom:-15px;left:20px}.flashXAxis{right:-15px;top:20px;transform:rotate(90deg)}.flashYAxis{top:-15px;right:20px}@media(max-width: 749px){.decorationWrapper{width:60px;height:60px}.decorationWrapper.oval{width:80px;height:60px}}